.c-wrapper {
    padding:50px;
    display: flex;
}

.c-left {
    flex:1;
    
}
.c-left {
    flex:1;
    
}
.c-title{
    font-size: 60px;
    width: 80%;
}
.c-info-item{
    color:azure;
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;


}
.c-right {
    flex:1;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.c-icon {
    width: 30px;
    height: 30px;
    margin-right:20 px;
    border-radius: 10px;
}

.c-desc{
    font-weight:200;
}
.form{
    margin-top: 20px;
}

input {
    width: 100%;
    height: 40px;
    border:none;
    border-bottom: 1px solid  black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    border-radius: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 20px;
    padding-left: 10px;
    border-radius: 10px;
}

button{
    border: none;
    padding:15px;
    background-color: #E338BB;
    color: azure;
    font-weight: 200;
    border-radius: 10px;

}
@media screen and (max-width: 480px) {
    .c-wrapper {
      flex-direction: column;
      padding: 0px 50px;
    }
  
    .c-title {
      font-size: 30px;
    }
  
    .c-info-item {
      margin: 20px 0px;
      width: 100%;
    }
  
    .c-desc {
      display: none;
    }
  
    form {
      margin-top: 0px;
      display: flex;
      flex-wrap: wrap;
    }
  
    input {
      width: 35%;
      height: 40px;
      margin: 10px;
      margin-left: 0;
    }
  
    button {
      margin-top: 10px;
    }
  }